<template>
  <div class="jiesuan">
    <p>合计：{{numprice}} 元</p>
    <button>结算（{{numall}}）</button>
  </div>

</template>
<script>
  export default {
    props:{
      numall:{
        type:Number,
        default:false
      },
      numprice:{
          type:Number,
          default:false
      }
    },
    data(){
      return {}
    },
    created(){},
    methods:{

    },
    computed:{},
    components:{}
  }
</script>
<style scoped lang="less">
  .jiesuan{
    position: fixed;
    bottom: 50px;
    left: 0;
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 1000;
    background-color:#ac2925 ;
    p{
      color: #ffffff;
      font-size: 16px;
      line-height: 40px;
      padding-left: 10px;
    }
    button{
      background:#880000;
      outline:none ;
      border: none;
      color: #ffffff;
      font-size: 16px;
      line-height: 40px;
      padding:0 10px;
    }

  }
</style>



