<template>
  <div>
    <Mheader>购物</Mheader>
    <div class="content">
    <ul class="bookList">
      <li v-for="(book,index) in cartList" >
        <img :src="book.bookCover" alt="">
        <div class="bookInfo">
        <h3>{{book.bookName}}</h3>
        <p>单价：{{book.bookPrice}}</p>
        <p>
            <button class="btn1" @click="reduce(book)">-</button>
            {{book.bookCount}}
          <button class="btn2" @click="add(book)">+</button>
          </p>
        <button class="colect" @click="remove(book)">删除</button>
        </div>
      </li>
    </ul>
    <Jiesuan :numall="count" :numprice="price"></Jiesuan>
    </div>
  </div>
</template>
<script>
  import Mheader from '../base/Mheader.vue'
  import Jiesuan from '../base/Jiesuan.vue'
  import {mapState,mapGetters} from 'vuex'
  import * as Type from '../store/mutations-type'
  export default {
    data(){
      return {
      }
    },
    created(){
    },
    watch:{
    },
    methods:{
        add(book){
            this.$store.commit(Type.ADD_CART,book);
        },
        reduce(book){
            this.$store.commit(Type.REDUCE_CART,book);
        },
        remove(book){
            this.$store.commit(Type.REMOVE_CART,book)
        }
    },
    computed:{
//      mapState(['cartList'])
      cartList(){
        return this.$store.state.cartList;
      },
      ...mapGetters(['count']),
      ...mapGetters(['price'])
    },
    components:{Mheader,Jiesuan}
  }
</script>
<style scoped lang="less">
  .bookList{
    padding:10px;
    padding-top: 20px;
    padding-bottom: 150px;
    li{
      position: relative;
      overflow: hidden;
      padding-bottom: 20px;
      padding-top: 20px;
      border-bottom: 1px dashed #cccccc;
    }
    img{
      width: 38%;
      float: left;
    }
    .bookInfo {
      position: relative;
      width: 60%;
      float: left;
      h3 {
        font-size: 16px;
        line-height: 20px;
        height: 40px;
        overflow: hidden;
        margin-top: 5px;
        margin-bottom: 5px;
      }
      p {
        display: inline-block;
        font-size: 14px;
        line-height: 18px;
        height: 18px;
        overflow: hidden;
        margin-right: 15px;
        margin-bottom: 15px;
        button{
          -webkit-appearance: none;
          border:none;
          outline: none;
          width: 15px;
          height: 15px;
          line-height: 15px;
          text-align: center;
          background-color: #880000;
          color: #ffffff;
        }
        .btn1{
          margin-right: 10px;
        }
        .btn2{
          margin-left: 10px;
        }
      }
      span {
        color: red;
        display: block;
      }
      .colect {
        display: block;
        padding:2px 10px;
        appearance: none;
        border:none;
        outline: none;
        background-color: orangered;
        color: #ffffff;
        border-radius: 5px;
        margin-top: 10px;
      }

    }
  }
</style>



